.container {
  max-width: 1200px;
  margin: auto;
}

.footer {
  height: 177px;
  flex-direction: column;
  display: flex;
}

.footer_pagination {
  height: 100px;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  list-style: none;
  display: flex;
}

.footer_information {
  width: 100vw;
  height: 77px;
  background-color: #f7f7f7;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-left: calc(-50vw - 50%);
  margin-right: calc(-50vw - 50%);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.16;
  display: flex;
  position: relative;
  left: 100%;
}

.information_icon {
  width: 14px;
  height: 12.88px;
}

.pagination_links {
  width: 40px;
  height: 40px;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.pagination_links li {
  margin: 0 5px;
}

.pagination_links a {
  color: #333;
  background-color: #ff6b08;
  border-radius: 5px;
  padding: 5px 10px;
  text-decoration: none;
  display: block;
}

.pagination_links:hover, .pagination_links a.active {
  color: #fff;
  background-color: #ff6b08;
}

.pagination_prev, .pagination_next {
  width: 40px;
  height: 40px;
  color: #333;
  background-color: #f2f2f2;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 5px 10px;
  text-decoration: none;
  display: flex;
}

.pagination_prev:hover, .pagination_next:hover {
  color: #fff;
  fill: #fff;
  background-color: #ff6b08;
}

@media (max-width: 768px) {
  .container {
    width: 100vh;
  }

  .footer {
    height: auto;
    flex-direction: row;
    justify-content: space-between;
  }

  .footer_information {
    width: auto;
    margin: 0;
    padding: 20px;
    font-size: 14px;
    left: 0;
  }

  .footer_pagination {
    height: auto;
    margin: 20px 0;
  }

  .information_icon {
    width: 12px;
    height: 12px;
  }

  .pagination_links {
    height: auto;
  }

  .pagination_links li {
    width: auto;
    height: auto;
    padding: 5px;
  }

  .pagination_links a {
    padding: 5px;
    font-size: 14px;
  }

  .pagination_prev, .pagination_next {
    width: auto;
    height: auto;
    border-radius: 5px;
    padding: 5px;
  }
}

@media (max-width: 320px) {
  .footer {
    flex-direction: column;
  }

  .footer_information {
    margin: 10px 0;
  }

  .pagination_links {
    flex-wrap: wrap;
  }

  .pagination_links li {
    width: 50%;
  }
}

